function reducer(state,action){
    
    if(action.type==='INIT_MENU'){
        return {
            ...state,
            menu:action.menu,
            currentItems:action.currentItems,
        }
    }else if(action.type==='ADD_MENU_ITEMS'){
        return {
            ...state,
            menu:action.menu,
            currentItems:action.currentItems,
        }
    }else if(action.type==='MESSAGE_IS_SENT'){
        return {
            ...state,
            isMessageSended:action.isMessageSended
        }
    }else if(action.type==='FAVORITE_PICKED'){
        return {
            ...state,
            menu:action.menu,
            favoriteItemsCount:action.favoriteItemsCount
        }
    }else if(action.type==='FAVORITE_REMOVED'){
        return {
            ...state,
            menu:action.menu,
            favoriteItemsCount:action.favoriteItemsCount
        }
    }else{
        return state
    }
    
}



export default reducer;