import 'bootstrap/dist/css/bootstrap.min.css';
import './messageForm.scss';
import { Button, Form } from 'react-bootstrap';
import {useForm} from 'react-hook-form';
import Success from './Success';
import { connect } from 'react-redux';

function MessageForm(props) {
  const token='5430496647:AAFoN2hVkkVi3SSHBHwRmb9W0LFZoUQ5tkQ';
  const {
    reset,
    register, 
    formState:{
    errors,
    },
    handleSubmit
}=useForm();



function formHandler(data){
  const urlToSendMessage=`https://api.telegram.org/bot5430496647:AAFoN2hVkkVi3SSHBHwRmb9W0LFZoUQ5tkQ/sendMessage?chat_id=759178123&text=`
  
  let name=data.name;
  let phone=data.phone;
  let text=data.message;
  let message=`name: ${name}
              phone: ${phone}
              message: ${text}`
  fetch(`${urlToSendMessage}${message}`)

  props.dispatch({
    type:'MESSAGE_IS_SENT',
    isMessageSended:true
  });

  reset();
  
}



  return (
    <Form onSubmit={handleSubmit(formHandler)} >
    {/* ------------------------------- */}
      <Form.Group className="mb-2" controlId="nameInp">
        <Form.Label>Your name</Form.Label>
          <Form.Control 
                type="text" 
                placeholder="Enter your name" 
                {...register('name',
                {required:'enter your name please!'}
                )}/>
          <Form.Text className="text-muted">
            {errors?.name && <p style={{color:'red'}}>{errors.name.message}</p>}
          </Form.Text>
        </Form.Group>
    {/* -------------------------------- */}
        <Form.Group className="mb-2" controlId="phone">
          <Form.Label>Your phone number</Form.Label>
            <Form.Control 
              type="tel" 
              placeholder="Enter your phone number" 
              {...register('phone',
              {required:'enter your phone please'}
              )}/>
          <Form.Text className="text-muted">
            {errors?.phone && <p style={{color:'red'}}>{errors.phone.message}</p>}
          </Form.Text>
        </Form.Group>
    {/* ------------------------------- */}
        <Form.Group className="mb-2" controlId="message">
          <Form.Label>Message</Form.Label>
          <Form.Control 
            type="textarea" 
            placeholder="Enter you message" 
            {...register('message',   
            {required:'write some message'}
            )}/>
          <Form.Text className="text-muted">
            {errors?.message && <p style={{color:'red'}}>{errors.message.message}</p>}
          </Form.Text>
        </Form.Group>
        <Button 
          variant='success' 
          type="submit" 
          >
            Submit
        </Button>
        <Success/>
        
    </Form>
  );
}
function mapStateToProps(state){
  return {
    isMessageSended:state.isMessageSended
  }
}
export default connect(mapStateToProps)(MessageForm);


