import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import { connect } from 'react-redux';
import './foodCard.scss';

function FoodCard(props) {
  let itemsCount=props.favoriteItemsCount;
  let data=props.data;

  let [...menu]=props.menu;

function addToFavorites(e){

    let foodId=e.currentTarget.dataset.id;
    let modifiedMenu=menu.map((item)=>{
      if(item.id==foodId){
        item.isPicked='true'
        return item
      }else{
        return item
      }
    })
          itemsCount+=1;
          props.dispatch({
            type:'FAVORITE_PICKED',
            menu:modifiedMenu,
            favoriteItemsCount:itemsCount
          })
    }

  let button;
  if(data.isPicked=='true'){
    button=<Button variant='secondary' style={{ backgroundColor:'red',color:'white',}} data-id={data.id} onClick={(e)=>{
      addToFavorites(e);
    }} className='addFavoriteBtn' disabled={true}>Picked</Button>
  }else if(data.isPicked=='false'){
    button=<Button variant='secondary' style={{ backgroundColor:'green',color:'white',}} data-id={data.id} onClick={(e)=>{
      addToFavorites(e);  
    }} className='addFavoriteBtn'>☆</Button>
  }else{
    button=null;
  }


    return (
      <Card style={{ width: '18rem' }} key={data.id}>
        <Card.Img 
          variant="top"
          src={data.photo} 
         />
        <Card.Body>
          <Card.Title>{data.id}</Card.Title>
          <Card.Text>
            {data.description}
          </Card.Text>
          <Card.Text>
            <span className='price-weight_wrap'>
              <small className='weight'>
              {data.weight} g
            </small>
            <small className='price'>
              {data.price} uah
            </small>
            </span>
          </Card.Text>  
          {button}    
        </Card.Body>
      </Card>
    );
}

function mapStateToProps(state){
  return {
    menu:state.menu,
    favoriteItemsCount:state.favoriteItemsCount
  }
}

export default connect(mapStateToProps)(FoodCard);