import { initializeApp } from 'firebase/app';
import {getDatabase, limitToFirst, limitToLast, onValue, orderByChild, query, ref, startAt} from 'firebase/database';
import React, { useEffect, useState } from 'react';
import {Button} from 'react-bootstrap';
import { connect } from 'react-redux';
import { firebaseConfig } from '../firebase/firebaseConfig';
import './loadingBtn.scss';



function LoadingButton(props) {

  const [lastItem, setLastItem] = useState(null);

  function startPos(current){
    if(current<10){
      return '0'+current+1
    }else{
      return ''+current+1
    }
  }


  const [isLoading, setLoading] = useState(false);
  const app = initializeApp(firebaseConfig);
  const database = getDatabase(app);
  const [...newMenu]=props.menu;
  

  useEffect(() => {
  
      const recentMenuRef = query(
      ref(database,'menu'), 
      orderByChild('id'), 
      startAt(startPos(props.currentItems)),
      limitToFirst(6),
    );
  
    const menuRef = query(
      ref(database,'menu'),  
      limitToLast(1),
    );

    // this request just to know id of last item
    onValue(menuRef,(elem)=>{
      let resp=elem.val();
    // set id of last item into lastItem state
    for(let key in resp){
      setLastItem(resp[key].id)
    }
      
    })

    if (isLoading) {
      onValue(recentMenuRef,(elem)=>{
        let res=elem.val();
        for(let key in res){
          newMenu.push(res[key]);
        }
        props.dispatch({
          type:'ADD_MENU_ITEMS',
          menu:newMenu,
          currentItems:props.currentItems+6,
        })
        setLoading(false);
      })
    }
  }, [isLoading]);


  const handleClick = () => setLoading(true);
  if(props.currentItems<lastItem){
    return (
      <div className='load-btn__wrap'>
      <Button
        variant="success"
        disabled={isLoading}
        onClick={!isLoading ? handleClick : null}
      >
        {isLoading ? 'yammy…' : 'More dishes '}
      </Button>
      </div>
    );
  }else{
    return (
      null
    )
}
}

function mapStateToProps(state){
  return {
    menu:state.menu,
    currentItems:state.currentItems 
  }
}

export default connect(mapStateToProps)(LoadingButton);