import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import './favoriteCard.scss';
import trashIcon from '../Assets/trash-bin.png';
import { useEffect } from 'react';
import { connect } from 'react-redux';


function FavoriteCard(props) {
  let item=props.item;
  let itemsCount=props.favoriteItemsCount;
  let [...menu]=props.menu;

  useEffect(()=>{
    let deleteBtns=document.querySelectorAll('.delete-btn');
    deleteBtns.forEach((btn)=>{
      btn.addEventListener('click',(e)=>{

        let foodId=e.currentTarget.dataset.id;
        let modifiedMenu=menu.map((item)=>{
            if(item.id==foodId){
                item.isPicked='false'
                return item
            }else{
                return item
            }
        })//map
        itemsCount-=1;
          props.dispatch({
            type:'FAVORITE_REMOVED',
            menu:modifiedMenu,
            favoriteItemsCount:itemsCount
          })//dispatch
    })//add listener
    })//foreach
  })//useeffect


    return (
      <Card className='favorite-card' style={{ width: '18rem', backgroundImage:`url(${item.photo})` }}>
        <div className='img-wrap' >
        </div>
        <Card.Body>
          <Card.Title className='favorite-card-title'>{item.id}</Card.Title>
          <Card.Text className='favorite-card-text'>
            <span className='favorite-price-weight_wrap'>
              <small className='weight'>
              {item.weight} g
            </small>
            <small className='price'>
             {item.price} uah
            </small>
            </span>
          </Card.Text >
          <label htmlFor={`deleteBtn${item.id}`} className="favorite-btn-label">
            <img src={trashIcon} className='favorite-trash-btn' alt=''/>
            <Button variant='secondary' style={{display:'none'}} className='delete-btn' id={`deleteBtn${item.id}`} data-id={item.id}></Button>
          </label>
        </Card.Body>
      </Card>
    );
}
function mapStateToProps(state){
  return {
    menu:state.menu,
    favoriteItemsCount:state.favoriteItemsCount
  }
}


export default connect(mapStateToProps)(FavoriteCard);