import 'bootstrap/dist/css/bootstrap.min.css';
import './App.scss';
import AdvertiseSlider from './sections/AdvertiseSlider';
import Footer from './sections/Footer';
import Header from './sections/Header';
import Main from './sections/Main';
import { connect } from 'react-redux';
import { useEffect } from 'react';
import { initializeApp } from "firebase/app";
import { getDatabase, ref, query, limitToFirst, onValue} from "firebase/database";
import {firebaseConfig} from './firebase/firebaseConfig'




function App(props) {
  
const app = initializeApp(firebaseConfig);
const database = getDatabase(app);

useEffect(()=>{
  const recentMenuRef = query(ref(database,'menu'), limitToFirst(props.requestLimit));
  if(!props.menu.length>0){
    onValue(recentMenuRef,(elem)=>{
      props.dispatch({
        type:'INIT_MENU',
        menu:elem.val(),
        currentItems:6,
      })
    })
  }
})

  return (
    <div className="App">
      <Header/>
      <AdvertiseSlider/>
      <Main/>
      <Footer/>
    </div>
  );
}

function mapStateToProps(state){
  return {
    menu:state.menu,
    requestLimit:state.requestLimit,
    currentItems:state.currentItems
  }
}

export default connect(mapStateToProps)(App);
