import { useEffect, useState } from "react";
import { Button, Modal } from "react-bootstrap";
import { connect } from "react-redux";
import successIcon from '../Assets/success-icon.png';
import './success.scss';

function Success(props) {
    const [show, setShow] = useState(false);
    const handleClose = (() => {
        props.dispatch({
            type:'MESSAGE_IS_SENT',
            isMessageSended:false
          });
        setShow(false)}
        );

    useEffect(()=>{
        setShow(props.isMessageSended)
    })

    return (
        <>
        <Modal show={show} onHide={handleClose} className='success-modal'>
        <Modal.Header closeButton>
        <img src={successIcon} className='success-icon' alt=''/>
          <Modal.Title>
            Message was successfuly sent!
        </Modal.Title>
        </Modal.Header>

        <Modal.Body>
            Thank for your feed back. We will reply as soon as possible!
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </>
    );
  }
  function mapStateToProps(state){
    return {
        isMessageSended:state.isMessageSended
    }
  }

  export default connect(mapStateToProps)(Success);