import React, { useState } from 'react';
import { Button, Offcanvas} from 'react-bootstrap';
import './burgerMenu.scss';
import logo from '../Assets/logos/logo.png'
import burger from '../Assets/burger.png';

function BurgerMenu() {

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  function scrollToSect(sect){
    let clientRect=document.querySelector(`#${sect}`).getBoundingClientRect();
    window.scrollTo({
      top:clientRect.top-100,
      behavior:'smooth'
    })
    handleClose();
  }

return (
      <div className='burger'>
      <label htmlFor='burgerBtn'>
        <img src={burger} alt='' style={{width: 40, heigth:'auto'}} className='burgerIcon'/>
        <Button onClick={handleShow} id='burgerBtn'></Button>
      </label>

      <Offcanvas show={show} onHide={handleClose} data-bs-scroll="true" tabIndex="-1" id="offcanvasWithBothOptions" aria-labelledby="offcanvasWithBothOptionsLabel">
        <Offcanvas.Header closeButton>
          <span></span>
        </Offcanvas.Header>
        <Offcanvas.Body>
          <nav>
          <a href="#" onClick={()=>{
            scrollToSect('menuSect')
          }}>MENU</a>
          <a href="#" onClick={()=>{
            scrollToSect('mapSect')
          }}>ADDRESS</a>
          <a href="#" onClick={()=>{
            scrollToSect('groupSect')
          }}>OUR GROUP</a>
          <a href="#" onClick={()=>{
            scrollToSect('contactUsSect');    
          }}>CONTACT US</a>
          </nav>
          <div className='nav-logo_wrap'>
            <img src={logo} alt="" />
          </div>
        </Offcanvas.Body>
      </Offcanvas>
    </div>
  )
      
}

export default BurgerMenu;