import { useState } from "react";
import { Button, Modal } from "react-bootstrap";
import favorites from '../Assets/food-tray.png'
import './favoritePopUp.scss';
import FavoriteCard from '../elements/FavoriteCard';
import { connect } from "react-redux";
import { nanoid } from 'nanoid'


function FavoritePopUp(props) {
  



    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
   


    
    
    
    return (
        <>
        <label className="favorites-wrap" htmlFor='favorites'>
                  <img src={favorites} alt="" className="favoritesIcon"/>
                  <small>{props.favoriteItemsCount}</small>
                  <Button id='favorites' onClick={handleShow}></Button>
        </label>
      

      <Modal show={show} onHide={handleClose} className='favorites-modal'>
        <Modal.Header closeButton>
          <Modal.Title>Your favorites</Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <div className='favorite-food-card__wrap'>
              {props.menu.map((item)=>{
                if(item.isPicked=='true'){
                  return <FavoriteCard item={item} key={nanoid()}/>
                }
               else{
                  return null
                }
                  
              })}
              
            </div>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </>
    );
  }

 function mapStateToProps(state){
    return {
      menu:state.menu,
      favoriteItemsCount:state.favoriteItemsCount 
    }
 }

  export default connect(mapStateToProps)(FavoritePopUp);